import { Bloc } from '@felangel/bloc';
import HelperApiRepository from '~/src/repository/helper_repository';

export abstract class DonationSectionEvent {
}

export enum DonationType {
  Top,
  Latest,
}

export class PageStartEvent extends DonationSectionEvent {
  type: DonationType
  constructor(
    type: DonationType
  ) {
    super();
    this.type = type;
  }
}

export class NextPageEvent extends DonationSectionEvent {
}

export class PreviousPageEvent extends DonationSectionEvent {
}

export class DonationSectionState<T> {
  constructor(
    readonly isNextValid: boolean,
    readonly isPrevValid: boolean,
    readonly isLoading: boolean,
    readonly page: number,
    readonly datas: Array<T>,
  ) {
  }

  static empty() {
    return new DonationSectionState(
      true,
      false,
      false,
      0,
      []
    )
  }

  copyWith({
    isNextValid,
    isPrevValid,
    isLoading,
    page,
    datas,
  }: {
    isNextValid?: boolean,
    isPrevValid?: boolean,
    isLoading?: boolean,
    page?: number,
    datas?: Array<T>,
  }) {
    return new DonationFormState(
      isNextValid != null ? isNextValid : this.isNextValid,
      isPrevValid != null ? isPrevValid : this.isPrevValid,
      isLoading != null ? isLoading : this.isLoading,
      page != null ? page : this.page,
      datas != null ? datas : this.datas,
    )
  }
}

export class DonationSectionBloc<T> extends Bloc<DonationSectionEvent, DonationSectionState<T>> {
  initialState(): DonationSectionState<T> {
    return DonationSectionState.empty();
  }

  async *mapEventToState(event: DonationSectionEvent): AsyncIterableIterator<DonationSectionState> {
    if(event instanceof PageStartEvent) {
      yield* this._mapPageStartEventToState(event);
    } else if(event instanceof NextPageEvent) {
      yield* this._mapNextPageEventToState(event);
    } else if(event instanceof PreviousPageEvent) {
      yield* this._mapPreviousPageEventToState(event);
    }
  }

  async *_mapPageStartEventToState(event: PageStartEvent) {
    const currentState = this.currentState;
    yield currentState.copyWith({
      isLoading: true
    });
    // let data;
    if(event.type == DonationType.Top) {
      let datas = await HelperApiRepository.getTop(0);
      //data[0]
      yield currentState.copyWith({
        isLoading: false,
        isPrevValid: false,
        isNextValid: datas.length == 10,
        page: 0,
        datas: datas
      });
    }
    if(event.type == DonationType.Latest) {
      let datas = await HelperApiRepository.getLatest(0);
      //data[0]
      yield currentState.copyWith({
        isLoading: false,
        isPrevValid: false,
        isNextValid: datas.length == 10,
        page: 0,
        datas: datas
      });
    }
  }
  async *_mapNextPageEventToState(event: NextPageEvent) {
    const currentState = this.currentState;
    yield currentState.copyWith({
      isLoading: true
    });
    // let data;
    if(event.type == DonationType.Top) {
      let datas = await HelperApiRepository.getTop(currentState.page + 1);
      const finalPage = datas.length == 0 ? currentState.page : currentState.page + 1
      yield currentState.copyWith({
        isLoading: false,
        isPrevValid: finalPage !== 0,
        isNextValid: datas.length == 10,
        page: finalPage,
        datas: datas
      });
    }
    if(event.type == DonationType.Latest) {
      let datas = await HelperApiRepository.getLatest(currentState.page + 1);
      const finalPage = datas.length == 0 ? currentState.page : currentState.page + 1
      yield currentState.copyWith({
        isLoading: false,
        isPrevValid: finalPage !== 0,
        isNextValid: datas.length == 10,
        page: finalPage,
        datas: datas
      });
    }
  }
  async *_mapPreviousPageEventToState(event: PreviousPageEvent) {
    const currentState = this.currentState;
    yield currentState.copyWith({
      isLoading: true
    });
    // let data;
    if(event.type == DonationType.Top) {
      let datas = await HelperApiRepository.getTop(Math.max(0, currentState.page - 1));
      //data[0]
      yield currentState.copyWith({
        isLoading: false,
        isPrevValid: page !== 0,
        isNextValid: true,
        page: Math.max(0, currentState.page - 1),
        datas: datas
      });
    }
    if(event.type == DonationType.Latest) {
      let datas = await HelperApiRepository.getLatest(Math.max(0, currentState.page - 1));
      //data[0]
      yield currentState.copyWith({
        isLoading: false,
        isPrevValid: page !== 0,
        isNextValid: true,
        page: Math.max(0, currentState.page - 1),
        datas: datas
      });
    }
  }
}